@import "../../App.scss";

.contact-container {
  background-color: #505050;
  height: 220px;
  color: white;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  .footer-content-box {
    width: 65%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .footer-copyright-box {
      margin-top: 12px;
      height: 15%;
      display: flex;
      align-items: flex-end;
      border-top: 2px solid rgb(184, 184, 184);

      .footer-copyright {
        font-size: 12px;
      }
    }

    .footer-link-container {
      display: flex;
      height: 50%;

      @include phone {
        justify-content: center;
      }

      .footer-link {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 30px;
        width: 30px;
        border-radius: 50%;
        background-color: #b4d4d6;
        padding: 10px;
        margin: 8px;
        transition: all 0.2s ease-in-out;

        .link-txt {
          z-index: 2;
          position: absolute;
          bottom: 60px;
          color: white;
          text-decoration: none;
        }

        .footer-img {
          object-fit: cover;
          width: 40%;
        }
      }

      .footer-link:hover {
        background-color: white;
        transform: scale(1.1);
        cursor: pointer;
      }
    }
  }
}
